.featured {
   width: 100%;
   display: flex;
   justify-content: space-between;

}

.featuredItem {
   flex: 1;
   margin: 15px 20px 0 20px;
   padding: 30px;
   border-radius: 10px;
   cursor: pointer;
   -webkit-box-shadow: 0px 0px 15px -10px #000000; 
   box-shadow: 0px 0px 15px -10px rgba(0, 0, 0,0.75);
}

.featuredTitle {
   font-size: 20px;
}

.featuredMoneyContainer {
   margin: 10px 0px;
   display: flex;
   align-items: center;

}

.featuredMoney {
   font-size: 30px;
   font-weight: bold;
}

.featuredMoneyRate {
   display: flex;
   align-items: center;
   margin-left: 12px;
}

.featuredIcon {
   font-size: 14px;
   margin-left: 15px;
   color: red;
}

.featuredIcon.positive {
   color: green;
}
 
.featuredSub {

   font-size: 15px;
   color: gray;
}

@media screen and (max-width: 992px) {
   .featured{
      flex-direction: column;
      padding: 0;
      margin: 0;
   }
}